.hover-3-title{
  font-size: 40px;
}


/** utils **/

.hover {
  overflow: hidden;
  position: relative;
}

.hover-overlay {
    transition: all 0.4s;
  }

.hover img {
  transition: all 0.3s;
  width: 100%;
  height: 100%;
}

.hover-3::after {
    content: '';
    width: calc(100% - 3rem);
    height: calc(100% - 3rem);
    border: 1px solid #fff;
    position: absolute;
    top: 1.5rem;
    left: 1.5rem;
    z-index: 90;
    transition: all 0.3s;
    transform: scale(1.1);
    opacity: 0;
    display: block;
    opacity: 0;
  }

  .hover-3-content {
    position: absolute;
    top: 50%;
    left: 0;
    width: 100%;
    transform: translateY(-50%);
    text-align: center;
    z-index: 99;
    display: flex;
    align-items: center;
    flex-direction: column;
  }

  .contact-info-content {
    position: absolute;
    top: 45%;
    left: 0;
    width: 100%;
    transform: translateY(-50%);
    text-align: center;
    z-index: 99;
    display: flex;
    align-items: flex-start;
    flex-direction: column;
  }

  .hover-3-description {
    opacity: 0;
    transform: scale(1.3);
    transition: all 0.3s;
    text-decoration: none;
    color: white;
    font-size: 30px;
  }

  .hover-3-description:hover{
    color: rgb(187, 175, 175);
  }

  .hover-3 img {
    width: 110%;
    top: 50%;
    left: 50%;
  }

  .hover-3 .hover-overlay {
    background: rgba(0, 0, 0, 0.2);
  }

  .hover-3:hover img {
    width: 100%;
  }

  .hover-3:hover::after {
    opacity: 1;
    transform: none;
  }

  .hover-3:hover .hover-3-description {
    opacity: 1;
    transform: none;
  }

  .hover-3:hover .hover-overlay {
    background: rgba(0, 0, 0, 0.8);
  }

  .contact-info{
    text-align: left;
  }
